.response-card {
  display: flex;
  background-color: #f5f5f6;
  border-radius: 15px;
  margin-bottom: 20px;
  position: relative;
  & .feedback-wrapper:last-child {
   flex-grow: 1;
  }
}
.customer-photo {
  border-bottom-left-radius: 15px;
  border-top-left-radius: 15px;
}
.feedback-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 12px 15px;
  & .stars-rating {
    align-self: flex-end;
  }
}
.feedback {
  margin-top: 0;
  font-style: italic;
  font-size: 16px;
}
.link--big {
  font-size: 22px;
  margin-bottom: 3px;
}
.task {
  margin: 0;
  color: #777777;
}
.info-text {
  font-size: 14px;
  color: #777777;
  margin: 0;
  align-self: flex-end;
}
.response-wrapper {
  display: flex;
}
.reviews {
  font-size: 14px;
  padding-left: 50px;
  margin: 0;
  margin-top: 3px;
  position: relative;
  &::after {
    position: absolute;
    content: "";
    width: 16px;
    height: 16px;
    background-image: url("../img/like.svg");
    top: 0;
    left: 25px;
  }
}
.response-message {
  width: 416px;
  font-size: 16px;
  margin-top: 5px;
  margin-bottom: auto;
}
.button-popup {
  display: none;
}
