.response-card .button-popup {
    display: flex;
    flex-direction: column;
    position: absolute;
    z-index: -10;
    content: "";
    top: 22px;
    right: 0;

  transition: all .2s ease-in-out;
  transform: translateX(-90%);
}
.response-card:hover .button-popup {
  transform: translateX(0%);

  z-index: 2;
  content: "";
  right: -145px;
}